<template>
    <div>
        <g-appbar
            title="한국전력공사  T.B.M" 
            background="#f8f8fa"
            :left="true" :right="true"
        />
        <div class="g-area-top">
            <div class="d-flex align-center">
                <p class="g-step-now">{{ step }}</p>
                <div class="g-step-bar"></div>
                <p class="g-step">{{ totalStep }}</p>
            </div>
            <p class="s-title">
                PMIS 단말기를 이용하여 <br/>
                건강 상태를 확인해 주세요.
            </p>
        </div>
        <div class="g-progress">
            <v-progress-linear 
                :model-value="step"
                :buffer-value="progress"
                color="#7285f3"
                background-opacity="1"
            ></v-progress-linear>
        </div>
        <div class="g-area-middle"> 
            <div class="s-notice-box">
                <img class="s-notice-icon" src="@/assets/images/icon/i_caution.png"/>
                <p>
                    PMIS 단말기 정보를 불러와서 건강 상태 확인 여부를 표시합니다. 건강 상태 확인이 안될 경우 수기로 확인해 주시기 바랍니다.
                </p>
            </div>
            <div class="mb-16">
                <div 
                    v-for="(item, index) in list" :key="index" 
                    :class="boxStyle(index)"
                    @click="onClickItem(index)"
                >
                    <div class="d-flex align-center">
                        <img class="s-icon-person" :src="getPersonIcon(item.id)"/>
                        <p class="color_black size_14 font_semi_bold">{{ item.name }}</p>
                    </div>
                    <div class="d-flex align-center">
                        <p class="size_13 font_semi_bold" :class="textStyle(index)">확인</p>
                        <img class="s-notice-icon" :src="checkImg(index)"/>
                    </div>
                </div>
            </div>
            <div class="g-fixed-btn">
                <basic-btn                     
                    state="next"
                    :dimBtn="!allConfirm"
                    @onClick="onClickNext"
                />
            </div>
        </div>
    </div>
</template>

<script>
import BasicBtn from '@/components/button/BasicBtn.vue'

export default {
    name: 'Step01',
    components: { BasicBtn },
    data() {
        return {
            step: 2,
            totalStep: 6,
            list: [
                { id: 1 , name: '김범수', selected: false },
                { id: 2 , name: '이창재', selected: false },
                { id: 3 , name: '정용진', selected: false },
                { id: 4 , name: '최원준', selected: false },
                { id: 5 , name: '김한규', selected: false },
                // { id: 6 , name: '이예진', selected: false },
                // { id: 7 , name: '고도연', selected: false },
                // { id: 8 , name: '김범수', selected: false },
            ]
        }
    },
    computed: {
        progress() {
            return Number((this.step) / (this.totalStep)) * 100;
        },
        allConfirm() {
            return this.list.every(item => item.selected === true);
        },
    },
    methods: {
        onClickItem(index) {
            const item = this.list[index]
            item.selected = !item.selected
        },
        boxStyle(index) {
            return this.list[index].selected ? "s-list-selectedBox" : "s-list-box"
        },
        textStyle(index) {
            return this.list[index].selected ? "color_primary_4" : "color_gray_3"
        },
        checkImg(index) {
            const blueCheck = require('@/assets/images/icon/i_check_blue.png')
            const check = require('@/assets/images/icon/i_check.png')
            return this.list[index].selected ? blueCheck : check
        },
        getPersonIcon(id) {
            return require(`@/assets/images/icon/i_person_${id}.png`)
        },
        onClickNext() {
            if (this.allConfirm) {
                this.$router.push('/TBM/Step03')
            }
        }
    }
}
</script>

<style scoped lang="scss">
.s {
    &-title {
        font-family: 'Pretendard Bold';
        font-size: 20px;
        color: #fff;
        line-height: 1.5;
        letter-spacing: -0.5px;
        margin: 16px 0 6px;
    }
    &-sub {
        font-family: 'Pretendard Medium';
        font-size: 14px;
        color: #777e8c;
        line-height: 1.43;
        word-break: keep-all;
    }
    &-notice {
        &-box {
            font-family: 'Pretendard Medium';
            word-break: keep-all;
            font-size: 14px;
            margin-bottom: 16px;
            color: #777e8c;
            display: flex;
        }
        &-icon {
            width: 20px;
            height: 20px;
            margin-right: 4px;
        }
    }
    &-list {
        &-box {
            padding: 8px 12px;
            border-radius: 8px;
            margin-bottom: 5px;
            display: flex;
            justify-content: space-between;
            background-color: #f5f5f5;
        }
        &-selectedBox {
            padding: 8px 12px;
            border-radius: 8px;
            margin-bottom: 5px;
            display: flex;
            justify-content: space-between;
            background-color: rgba(69, 94, 238, 0.1);

        }
    }
    &-icon-person {
        width: 28px;
        height: 28px;
        margin-right: 8px;
    }
}
</style>